import React, { useState, useEffect } from 'react';
import { useParams } from 'react-router-dom';

function ChampionDetails() {
    const { name } = useParams();
    const [streamers, setStreamers] = useState([]);

    useEffect(() => {
        // Envoyer une requête au serveur pour récupérer les streamers affiliés au champion
        fetch(`/api/champion/${name}`)
            .then(response => response.json())
            .then(data => {
                console.log('Données reçues:', data); // Test des données reçues

                setStreamers(data); // Mettre à jour l'état avec les données des streamers
            })
            .catch(error => console.error('Erreur lors de la récupération des streamers:', error));
    }, [name]); // Effectue la requête à chaque changement de nom de champion

    return (
        <div>
            <h1>{name}</h1>
            <h2>Streamers affiliés :</h2>
            <ul>
                {streamers.map(streamer => (
                    <li key={streamer.id}>
                        <a href={streamer.twitch} target="_blank" rel="noopener noreferrer">{streamer.name}</a>
                    </li>
                ))}
            </ul>
        </div>
    );
}

export default ChampionDetails;
